.ChatInput {
  border-radius: 10px;

  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translate(-50%);

  width: 40vw;
  height: 5vh;

  font-size: 1.5rem;

  padding: 1vh 2vh;

  background-color: rgb(33, 33, 33);

  border: 1px solid #ffffffa9;
}
.ChatInput:hover {
  border-color: #fff;
}
